import React from 'react';
import Info from './Info';
import axios from 'axios';
import { useCart } from './hooks/useCart'

const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms))

function Drawer({onClose, onRemove, items = []}) {
  
  const [isOrderComplete, setIsOrderComplete] = React.useState(false);
  const [isloading, setIsloading] = React.useState(false);
  const [orderId, setOrderId] = React.useState(false);
  const { cartItems, setCartItems, totalPrice } = useCart();

  const onClickOrder = async () => {
    try {
      setIsloading(true);
      const {data} = await axios.post('https://60d83b626f13520017a681d3.mockapi.io/orders', {
        items: cartItems });
      
      setOrderId(data.id);
      setIsOrderComplete(true);
      setCartItems([]); 

      for (let i = 0; i < cartItems.length; i++) {
        const item = cartItems[i];
        await axios.delete('https://60d83b626f13520017a681d3.mockapi.io/cart', item.id);
        await delay(1000);
      }
      
    } catch (err) {
      console.log('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –∑–∞–∫–∞–∑–∞');
    }
    setIsloading(false);
  }

    return (
    <div className="overlay">
        <div className="drawer">
          <h2 className="d-flex justify-between mb-20">–ö–æ—Ä–∑–∏–Ω–∞
          <img className="removeBtn" src="/img/btn-remove.svg" alt="Close" onClick={onClose}/></h2>

          {
            items.length > 0 ? (
              <div className="d-flex flex-column flex">
                <div className="items">
              {items.map((obj) => (
                <div key={obj.id} className="cartItem d-flex align-center mb-20">
                  <div style={{ backgroundImage: `url(${obj.imageUrl})` }} className="cartItemImg">
                  </div>  
  
                  <div className="mr-20 flex">
                    <p className="mb-5">{obj.name}</p>
                    <b>{obj.price} —Ä—É–±.</b>
                  </div>
  
                  <img 
                      className="removeBtn" 
                      src="img/btn-remove.svg" 
                      alt="Remove" 
                      onClick={() => onRemove(obj.id)}/>
                </div>))
            }
          </div>

          <div className="cartTotalBlock">
            <ul>
              <li className="d-flex">
                <span>–ò—Ç–æ–≥–æ:</span>
                <div></div>
                <b>{totalPrice} —Ä—É–±.</b>
              </li>
              <li className="d-flex">
                <span>–ù–∞–ª–æ–≥ 5%:</span>
                <div></div>
                <b>{totalPrice / 100 * 5} —Ä—É–±.</b>
              </li>
            </ul>
            <button disabled={isloading} onClick={onClickOrder} className="greenButton">–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑ <img src="img/arrow.svg" alt="Arrow" /></button>
          </div>
              </div>
            ) : (<Info 
              title={isOrderComplete ? "–ó–∞–∫–∞–∑ –æ—Ñ–æ—Ä–º–ª–µ–Ω!" : "–ö–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞—è" }
              description={isOrderComplete? `–í–∞—à –∑–∞–∫–∞–∑ ‚Ññ${Number(orderId)} —Å–∫–æ—Ä–æ –±—É–¥–µ—Ç –ø–µ—Ä–µ–¥–∞–Ω –∫—É—Ä—å–µ—Å–∫–æ–π –¥–æ—Å—Ç–∞–≤–∫–µ` : "–î–æ–±–∞–≤—å—Ç–µ —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É –ø–∞—Ä—É –∫—Ä–æ—Å—Å–æ–≤–æ–∫, —á—Ç–æ–±—ã —Å–¥–µ–ª–∞—Ç—å –∑–∞–∫–∞–∑."} 
              imageUrl={isOrderComplete? "img/complete-order.jpg" :"img/empty-cart.jpg"}/>)
          }
  
        </div>
    </div>
    )
}

export default Drawer;